/* form styles */

.formMessage {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  border-top: 1px solid #eee3e3;
}

.formMessage > svg {
  position: absolute;
  right: 5%;
  width: 12px;
  height: 12px;
  fill: #607d7d;
}

.dataEntry {
  width: 90%;
  height: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 20px;

  border: 1px solid #eee3e3;
  outline: none;
  border-radius: 25px;
}
